import React from "react";
import { Helmet } from "react-helmet";

/**
 * Ref : https://velog.io/@byseop/SPA%EC%97%90%EC%84%9C-%EC%84%9C%EB%B2%84%EC%82%AC%EC%9D%B4%EB%93%9C-%EB%A0%8C%EB%8D%94%EB%A7%81%EC%9D%84-%EA%B5%AC%EC%B6%95%ED%95%98%EC%A7%80-%EC%95%8A%EA%B3%A0-SEO-%EC%B5%9C%EC%A0%81%ED%99%94%ED%95%98%EA%B8%B0
 */

const locales = {
	en: "en_US",
	ko: "ko_KR",
};

const meta = {
	title: "Todo MVC",
	lang: locales.ko,
};

export default function Meta() {
	return (
		<Helmet>
			<html lang={meta.lang} />
			<title>{meta.title}</title>
			<meta name="description" content="todo application" />
			<meta property="og:title" content={meta.title} />
			<meta property="og:locale" content={meta.lang} />
		</Helmet>
	);
}
